.grid {
  display: grid;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-columns: repeat(8, 12.5%);
  height: 100%;

  & > button {
    align-items: center;
    display: flex;
    justify-content: center;
    height: 100%;

    &:nth-child(8n) {
      border-right: none;
    }

    &:before {
      display: block;
      content: '';
      height: 0;
      padding-bottom: 100%;
    }
  }
}
