$border: 1px solid #eee;

.sceneEditorToolbar {
  display: grid;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-columns: repeat(8, 12.5%);

  & > button {
    align-items: center;
    border-right: $border;
    display: flex;
    justify-content: center;

    &:nth-child(8n) {
      border-right: none;
    }
    
    &::before {
      content: '';
      display: inline-block;
      width: 1px;
      height: 0;
      padding-bottom: 100%;
    }
  }
}
